import React, { useState, useEffect } from 'react';
import { AddressInput, SearchIcon, AddressBtn } from './AddressForm.styled';


export const AddressForm = ({ onAddressSelect, onUserLocationSelect }) => {
    const [address, setAddress] = useState('');
    const [userLocation, setUserLocation] = useState(null);
  
    useEffect(() => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const { latitude, longitude } = position.coords;
            setUserLocation({ lat: latitude, lng: longitude });
            onUserLocationSelect(userLocation);
          },
          (error) => {
            console.error('–ü–æ–º–∏–ª–∫–∞ –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –≥–µ–æ–ª–æ–∫–∞—Ü—ñ—ó:', error);
          }
        );
      } else {
        console.error('–ì–µ–æ–ª–æ–∫–∞—Ü—ñ—è –Ω–µ –ø—ñ–¥—Ç—Ä–∏–º—É—î—Ç—å—Å—è –≤–∞—à–∏–º –±—Ä–∞—É–∑–µ—Ä–æ–º.');
      }
    }, [onUserLocationSelect, userLocation]);
  
    const handleAddressChange = (event) => {
      setAddress(event.target.value);
    };
  
    const handleFormSubmit = (event) => {
      event.preventDefault();
      const geocoder = new window.google.maps.Geocoder();
      geocoder.geocode({ address }, (results, status) => {
        if (status === 'OK' && results[0]) {
          const { lat, lng } = results[0].geometry.location;
          onAddressSelect({ lat: lat(), lng: lng() });
        } else {
          console.error('–ù–µ –≤–¥–∞–ª–æ—Å—è –∑–Ω–∞–π—Ç–∏ –¥–∞–Ω—É –∞–¥—Ä–µ—Å—É.');
        }
      });
    };
  
    return (
      <form onSubmit={handleFormSubmit}>
        <AddressInput type="text" value={address} onChange={handleAddressChange} placeholder="–í–≤–µ–¥—ñ—Ç—å –∞–¥—Ä–µ—Å—É" />
        <AddressBtn type="submit"><SearchIcon/></AddressBtn>
      </form>
    );
  };

